.container {
    margin: 2em auto;
    grid-column-start: 1;
    grid-column-end: 4;
    width: 90%;
}
.row {
    display: grid;
    border: 1px solid black;
    margin: 0.5em 0
}
@media (min-width: 740px) {
    .row {
        grid-template-columns: 1.5fr 0.3fr 3fr 1.2fr 2fr 1fr;
        grid-template-areas:
        "time weeknum subject type teacher room";
    }
    button {
        grid-column-start: 1;
        grid-column-end: 7;
        width: 30%;
        margin: 0 auto;
        justify-self: center;
    }
}
@media (max-width: 739px) {
    .row {
        grid-template-columns: 1fr 2fr 1fr;
        grid-template-areas:
        "time subject room"
        "weeknum teacher type";
    }
    button {
        grid-column-start: 1;
        grid-column-end: 4;
        width: 60%;
        margin: 0 auto;
        justify-self: center;
    }
}
.main {
    max-width: 90%;
    margin: 0 auto;
    display: grid;
    grid-template-columns: 1fr 2fr 1fr;
}
.main p {
    text-align: center;
    grid-column-start: 2;
    grid-column-end: 3;
}
.main:nth-child(2nd) {
    background: #000;
}
.time {
    grid-area: time;
}
.weeknum {
    grid-area: weeknum
} 
.subject {
    grid-area:subject;
}
.type {
    grid-area: type
}
.teacher {
    grid-area: teacher;
}
.room {
    grid-area: room;
}
.arrow {
    display: flex;
    align-items: center;
}
.left {
    transform: rotate(-180deg);
    justify-self: left;
}
.right {
    justify-self: right;
}
button {
    font-size: 1.2em;
    margin: 0 auto;
    transition: all 0.2s;
    display: inline-block;
    padding: 0.35em 1.2em;
    border: 0.1em solid #ffffff;
    margin: 0 0.3em 0.3em 0;
    border-radius: 0.12em;
    box-sizing: border-box;
    text-decoration: none;
    font-weight: 300;
    color: #ffffff;
    text-align: center;
    background: #000;
  }
button:hover {
    color: #000000;
    background-color: #ffffff;
    border: 0.1em solid #000;
  }