.main {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0,0,0,0.4);
    display: flex;
    justify-content: center;
    align-items: center;
}
.container {
    display: grid;
    padding: 24px;
    grid-template-columns: 1fr 1fr 1fr;
    background: #c4c4c4;
    border-radius: 0.3em;
    gap: 0.3em;
}
.container div {
    background: #fff;
    border-radius: 0.3em;
    text-align: center;
    padding: 0.5em;
}